export default () => {
	const inputs = document.querySelectorAll('.input_digit-pretify');

	const mask = (value) => {
		let output = [];

		for (let i = 0; i < value.length; i++) {
			if (i !== 0 && i % 4 === 0) {
				output.push(' ')
			}
			output.push(value[i]);
		}

		return output.join[''];
	}

	const unmask = (value) => {
		var output = value.replace(new RegExp(/[^\d]/, 'g'), '');
		return output;
	}

	const keydownHandler = (e) => {
		oldValue = e.target.value;
	}
	
	const inputHandler = (e) => {
		let el = e.target,
			newValue = el.value;

		newValue = unmask(newValue);
		
		if(newValue.match(regex)) {
			newValue = mask(newValue);
			el.value = newValue;
		} else {
			el.value = oldValue;
		}
	}
}


